const fixedHeader = () => {
    const body = document.querySelector('body');
    const header = document.querySelector('.header');
    const modalBtns = document.querySelectorAll('.header .modal-btn');
    const links = document.querySelectorAll('.header__menu .header_link');
    let prevScrollPosition = window.pageYOffset || document.documentElement.scrollTop;
    let isFixed = false;
    let scrollCounter = 0; //   
    const scrollThreshold = 25; //  ,    
    let clickLinkFlag = false;
    let isScrolling = false;
    let timeout;

    function handleScroll() {
      //  ,      
      isScrolling = true;
       //    ,  
      clearTimeout(timeout);

      //     200 
      timeout = setTimeout(function () {
        // ,  ,    
        // console.log('  ');
        //  ,    
        isScrolling = false;
        clickLinkFlag = false;
      }, 200);

    }
    

    window.addEventListener('scroll', handleScroll);


    links.forEach(link => {
      link.addEventListener('click', () => {
        clickLinkFlag = true;
      })
    })
    

    function fixHeader() {
      // console.log(clickLinkFlag);
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

      if (scrollTop > prevScrollPosition) {
        //   (scroll down)
      if (!isFixed) {
        header.classList.add('fixed');
        body.style.marginTop = `${header.offsetHeight}px`;
        isFixed = true;
      }
      } else {
        //   (scroll up)
        if (isFixed) {
          isFixed = false;
        }
      }

      //      
      if (!isFixed) {
        scrollCounter++;
      } else {
        scrollCounter = 0;
      }
      
      if (clickLinkFlag) {
        //     
       
         //    
      } else {
         //  ,     
          if (scrollCounter >= scrollThreshold) {
            header.classList.remove('fixed');
            body.style.marginTop = `0px`;
            isFixed = false;
            scrollCounter = 0; //  
            modalBtns.forEach(btn => {
              btn.addEventListener('click', () => {
                if (isFixed) {
                  header.classList.remove('fixed');
                  body.style.marginTop = `0px`;
                  isFixed = false;
                  scrollCounter = 0;
                }
              });
            })
          }

        //     "fixed"   
        // ... (    )
      }



     

     
      
      prevScrollPosition = scrollTop;
    }
      //    
      window.addEventListener('scroll', fixHeader);
};

export default fixedHeader;